import React, { useState, useRef, useEffect } from "react";
import LoginDiv from "../styles/loginCss";
import { useNavigate } from "react-router-dom";
import firebase from "../firebase";

const Login = () => {
  const [email, setEmail] = useState("bmj44571627@gmail.com");
  const [pw, setPw] = useState("");
  const [errMsg, setErrMsg] = useState("");
  const navigate = useNavigate();
  const inputRef = useRef();

  useEffect(() => {
    inputRef.current.focus();
  }, []);

  // 

  const signInFunc = (e) => {
    e.preventDefault();
    if (!email || !pw) {
      return alert("  ");
    }

    const tempUser = firebase.auth();
    tempUser
      .signInWithEmailAndPassword(email, pw)
      .then((userCredential) => {
        //  
        const user = userCredential.user;
        console.log(user);
        // Redux  App store  
        // component state  
        navigate("/todo");
      })
      .catch((error) => {
        //  
        const errorCode = error.code;
        if (
          errorCode === "auth/wrong-password" ||
          errorCode === "auth/user-not-found"
        ) {
          setErrMsg("");
        }
      });
  };

  return (
    <div className="p-6 m-4 shadow">
      <h2>Login</h2>
      <LoginDiv>
        <form>
          <label></label>
          <input
            ref={inputRef}
            type="email"
            required
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <label></label>
          <input
            type="password"
            required
            autoComplete="off"
            maxLength={10}
            value={pw}
            onChange={(e) => setPw(e.target.value)}
          />
          {errMsg !== "" && <p style={{ color: "skyblue" }}>{errMsg}</p>}
          <button onClick={(e) => signInFunc(e)}></button>
          <button
            onClick={(e) => {
              e.preventDefault();
              navigate("/signup");
            }}
          >
            
          </button>
        </form>
      </LoginDiv>
    </div>
  );
};

export default Login;
