import React, { useState, useCallback, useEffect } from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import Dropdown from "react-bootstrap/Dropdown";
import DropdownButton from "react-bootstrap/DropdownButton";
import List from "../components/List";
import Form from "../components/Form";
// import Loading from "../components/Loading.jsx";
// import Loading2 from "../components/Loading2.jsx";
import LoadingSpinner from "../components/LoadingSpinner";
import axios from "axios";

//1.   
import { useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const Todo = () => {
  const [todoData, setTodoData] = useState([]);
  const [todoValue, setTodoValue] = useState("");
  const [loading, setLoading] = useState(false);

  const user = useSelector((state) => state.user);
  const navigate = useNavigate();
  console.log("user", user);

  useEffect(() => {
    //   
    console.log(user);
    if (user.accessToken === "") {
      //  
      alert("");
      navigate("/login");
    }
  }, [user, navigate]);

  //  

  const [sort, setSort] = useState("");

  useEffect(() => {
    setSkip(0);
    setTodoData([]);
    getList(search, 0);
  }, [sort]);

  // 
  const [search, setSearch] = useState("");

  const searchHandler = () => {
    setSkip(0);
    setTodoData([]);
    getList(search, 0);
  };

  const getList = (_word = "", _stIndex = 0) => {    
    setSkip(0);
    //  
    
    setLoading(true);
    setSkipToggle(false);

    const body = {
      sort: sort,
      search: _word,
      //  
      uid: user.uid,
      skip: _stIndex,
    };
    axios
      .post("/api/post/list", body)
      .then((response) => {
        // console.log(response.data);
        //   
        if (response.data.success) {
          setTodoData(response.data.initTodo);
          //  skip  .
          setSkip(response.data.initTodo.length);
          if (response.data.total>5) {
            setSkipToggle(true);
          }
        }
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  
  const getListGo = (_word = "", _stIndex = 0) => {
    setLoading(true);

    const body = {
      sort: sort,
      search: _word,
      //  
      uid: user.uid,
      skip: _stIndex,
    };
    axios
      .post("/api/post/list", body)
      .then((response) => {
        // console.log(response.data);
        //   
        if (response.data.success) {
          const newArr = response.data.initTodo;
          setTodoData([...todoData, ...newArr]);
          //  skip  .
          setSkip(skip + newArr.length);
          if (newArr.length < 5) {
            setSkipToggle(false);
          }
        }
        //  
        setLoading(false);
      })
      .catch((error) => {
        console.log(error);
      });
  };

  //  
  const [skip, setSkip] = useState(0);
  const [skipToggle, setSkipToggle] = useState(true);

  const getListMore = () => {
    getListGo(search, skip);
  };

  useEffect(() => {
    getList("", skip);
  }, []);

  const deleteData = useCallback(
    (id) => {
      if (window.confirm("?")) {
        let body = {
          id: id,
        };
        setLoading(true);
        axios
          .post("/api/post/delete", body)
          .then((response) => {
            console.log(response);
            const nowTodo = todoData.filter((data) => data.id !== id);
            //axios  MongoDB  
            setTodoData(nowTodo);
            setLoading(false);
          })
          .catch((error) => {
            console.log(error);
          });
      }
    },
    [todoData, setTodoData]
  );

  const addTodoSubmit = (e) => {
    e.preventDefault();
    console.log("", todoValue);
    setLoading(true);
    const addTodo = {
      id: Date.now(),
      title: todoValue,
      completed: false, //    
      // 1. DB : Server/Model/TodoModel Schema (ObjectID)
      uid: user.uid,
      //   
    };
    axios
      .post("/api/post/submit", { ...addTodo })
      .then((response) => {
        if (response.data.success) {
          // setTodoData([...todoData, addTodo]);
          setTodoValue("");
          setSkip(0);
          getList("", 0);
          alert("");
        } else {
          alert("");
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };

  const deleteAll = () => {
    if (window.confirm(" ?")) {
      axios
        .post("/api/post/deleteAll")
        .then(() => {
          setSkip(0);
          setTodoData([]);
          setLoading(true);
        })
        .catch((error) => console.log(error));
      setTodoData([]);
    }
  };

  return (
    <div className="flex justify-center w-full">
      <div className="w-full p-6 m-4 bg-blue-500 rounded shadow">
        <div className="flex justify-between mb-3">
          <h1> </h1>
          <button onClick={deleteAll}> </button>
        </div>

        <div className="flex justify-between mb-2">
          <DropdownButton title={sort} variant="outline-secondary">
            <Dropdown.Item onClick={() => setSort("")}>
              
            </Dropdown.Item>
            <Dropdown.Item onClick={() => setSort("")}>
              
            </Dropdown.Item>
            <Dropdown.Item></Dropdown.Item>
          </DropdownButton>
          <div>
            <label></label>
            <input
              type="text"
              value={search}
              onChange={(e) => setSearch(e.currentTarget.value)}
              placeholder=""
              className="border-2"
              onKeyDown={(e) => {
                if (e.key === "Enter") {
                  searchHandler();
                }
              }}
            />
          </div>
        </div>

        <List
          todoData={todoData}
          setTodoData={setTodoData}
          deleteData={deleteData}
        />
        {skipToggle && (
          <div className="flex justify-center">
            <button
              className="p-2 text-blue-400 border-2 border-blue-400 rounded hover:text-white hover:bg-blue-400"
              onClick={() => getListMore()}
            >
              
            </button>
          </div>
        )}

        <Form
          todoValue={todoValue}
          addTodoSubmit={addTodoSubmit}
          setTodoValue={setTodoValue}
        />
      </div>
      {/* {loading && <Loading/>} */}
      {loading && <LoadingSpinner />}
    </div>
  );
};

export default Todo;
