import React, { useState, useRef, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import SignUpDiv from "../styles/signUpCss";
import firebase from "../firebase";
import axios from "axios";

const SignUp = () => {
  const [nickName, setNickName] = useState("");
  const [email, setEmail] = useState("");
  const [pw, setPw] = useState("");
  const [pwCheck, setpwCheck] = useState("");
  //   
  const [btFlag, setBtFlag] = useState(false);
  const navigate = useNavigate();
  const inputRef = useRef();

  useEffect(() => {
    inputRef.current.focus();
  }, []);

  //firebase  
  const registFunc = (e) => {
    e.preventDefault();
    if (!nickName) {
      return alert(" ");
    }
    if (!email) {
      return alert(" ");
    }
    if (!pw) {
      return alert(" ");
    }
    if (!pwCheck) {
      return alert("  ");
    }
    //   

    if (pw !== pwCheck) {
      return alert("  ");
    }

    if (!nameCheck) {
      return alert("  ");
    }

    //  
    setBtFlag(true);

    const createUser = firebase.auth();
    createUser
      .createUserWithEmailAndPassword(email, pw)
      .then((userCredential) => {
        const user = userCredential.user;
        user
          .updateProfile({ displayName: nickName })
          .then(() => {
            let body = {
              email: user.email,
              displayName: user.displayName,
              uid: user.uid,
            };
            axios
              .post("/api/user/register", body)
              .then((response) => {
                if (response.data.success) {
                  navigate("/login");
                } else {
                  console.log(" ");
                }
              })
              .catch((error) => {
                console.log(error);
              });
          })
          .catch((error) => {});
      })
      .catch((error) => {
        setBtFlag(false);
        const errorCode = error.code;
        const errorMessage = error.message;
        console.log(errorCode, errorMessage);
      });
  };

  // 2.   

  const [nameCheck, setNameCheck] = useState(false);

  const nameCheckFn = (e) => {
    e.preventDefault();
    //  
    if (!nickName) {
      return alert("  ");
    }
    //      
    const body = {
      displayName: nickName,
    };
    axios
      .post("/api/user/namecheck", body)
      .then((response) => {
        if(response.data.success){
          if(response.data.check){
            //   
            setNameCheck(true);
            alert(' .')
            // 
          }else{
            setNameCheck(false);
            alert('  ')
            //  
          }
        }
      })
      .catch((error) => {
        console.log(error);
      });
  };
  return (
    <div className="p-6 m-4 shadow">
      <h2>SignUp</h2>
      <SignUpDiv>
        <form>
          <label></label>
          <input
            ref={inputRef}
            type="text"
            value={nickName}
            maxLength={12}
            onChange={(e) => setNickName(e.target.value)}
          />
          <button onClick={(e) => nameCheckFn(e)}> </button>
          <label></label>
          <input
            type="email"
            required
            value={email}
            onChange={(e) => setEmail(e.target.value)}
          />
          <label></label>
          <input
            type="password"
            required
            value={pw}
            maxLength={16}
            minLength={10}
            autoComplete="off"
            onChange={(e) => setPw(e.target.value)}
          />
          <label> </label>
          <input
            type="password"
            required
            value={pwCheck}
            maxLength={16}
            minLength={10}
            autoComplete="off"
            onChange={(e) => setpwCheck(e.target.value)}
          />
          <button disabled={btFlag} onClick={(e) => registFunc(e)}>
            
          </button>
        </form>
      </SignUpDiv>
    </div>
  );
};

export default SignUp;
